<script>
import axios from 'axios';

import Header from '../../components/Header.vue'
import Sidebar from '../../components/Sidebar.vue'
import UserHeader from './UserHeader.vue';

export default {
    name: `UserList`,
    components: {
        Header,
        Sidebar,
        UserHeader
    },
    data() {
        return {
            userList: null,
            totalItems: null
        }
    },
    methods: {
        async getUserList() {
            await axios.get(`/user/get-all`)
                .then(res => {
                    if (res != null) {
                        this.userList = res.data.data
                        this.totalItems = res.data.totalItems
                    }
                }).catch(err => {
                    console.log(err);
                })
        },

        async deleteUser(userId) {
            if(confirm("Ban co mun xoa ngi nay khng?")){
                await axios.post(`/user/delete/` + userId)
                    .then(res => {
                        if (res != null) {
                            console.log(res.data);
                            this.getUserList();
                        }
                    }).catch(err => console.log(err))
            }
        },
        
        submitDelete: () => {
                confirm('Ban co mun xoa ngi nay khng?')
            }
    },
    created() {
        this.getUserList();
    },
    mounted() {
        afterRender();
    },
    // updated() {
    //     afterRender();
    // }
}
</script>

